/**
 * POST /api/schema-update — Live schema updates via Claude Haiku tool-use
 *
 * Body: { sectionHtml, currentSchemas, componentType }
 * Returns: { ok, schemas: [...] }
 *
 * Uses Claude Haiku (~130x cheaper than Sonnet) with tool-use to
 * automatically generate/update JSON-LD schemas from section HTML.
 */

interface Env {
  ANTHROPIC_API_KEY: string;
}

interface SchemaUpdateRequest {
  sectionHtml: string;
  currentSchemas: any[];
  componentType?: string;
}

const SCHEMA_TOOLS = [
  {
    name: 'set_schemas',
    description: 'Replace all JSON-LD schemas for this page. Use this to set the complete list of JSON-LD schema objects.',
    input_schema: {
      type: 'object' as const,
      properties: {
        schemas: {
          type: 'array' as const,
          description: 'Array of complete JSON-LD schema objects (each with @context, @type, and properties)',
          items: { type: 'object' as const },
        },
      },
      required: ['schemas'],
    },
  },
  {
    name: 'update_property',
    description: 'Update a single property on an existing schema by index.',
    input_schema: {
      type: 'object' as const,
      properties: {
        schemaIndex: {
          type: 'number' as const,
          description: 'Index of the schema to update in the currentSchemas array',
        },
        property: {
          type: 'string' as const,
          description: 'The schema property name to set (e.g. "name", "image", "description")',
        },
        value: {
          description: 'The value to set for this property (string, object, or array)',
        },
      },
      required: ['schemaIndex', 'property', 'value'],
    },
  },
  {
    name: 'add_schema',
    description: 'Add a new JSON-LD schema object to the page.',
    input_schema: {
      type: 'object' as const,
      properties: {
        schema: {
          type: 'object' as const,
          description: 'A complete JSON-LD schema object with @context, @type, and properties',
        },
      },
      required: ['schema'],
    },
  },
];

const SYSTEM_PROMPT = `You are a Schema.org JSON-LD expert. Given section HTML and current schemas, return updated JSON-LD that accurately reflects the page content.

RULES:
1. Always use https://schema.org as @context.
2. Cover all visible content: text (name, headline, description), images, links, ratings, prices, addresses, dates.
3. Choose the most specific @type (e.g. Product, LocalBusiness, Article, FAQPage, Service, WebPage, Organization).
4. If current schemas exist, update them to reflect content changes rather than replacing unnecessarily.
5. If no schemas exist, create appropriate ones based on the HTML content.
6. Use set_schemas when multiple schemas need updating, update_property for single-field changes, add_schema for new types.
7. Keep schemas concise — only include properties that have actual values in the HTML.
8. For images, use the full URL from src attributes.
9. For prices, include priceCurrency if detectable.`;

function executeToolCalls(toolCalls: any[], currentSchemas: any[]): any[] {
  let schemas = [...currentSchemas];

  for (const call of toolCalls) {
    switch (call.name) {
      case 'set_schemas':
        schemas = call.input.schemas || [];
        break;
      case 'update_property': {
        const idx = call.input.schemaIndex;
        if (idx >= 0 && idx < schemas.length) {
          schemas[idx] = { ...schemas[idx], [call.input.property]: call.input.value };
        }
        break;
      }
      case 'add_schema':
        if (call.input.schema) {
          schemas.push(call.input.schema);
        }
        break;
    }
  }

  return schemas;
}

export const onRequestPost: PagesFunction<Env> = async (context) => {
  const { env } = context;

  if (!env.ANTHROPIC_API_KEY) {
    return Response.json({ ok: false, error: 'ANTHROPIC_API_KEY not configured' }, { status: 500 });
  }

  let body: SchemaUpdateRequest;
  try {
    body = await context.request.json();
  } catch {
    return Response.json({ ok: false, error: 'Invalid JSON' }, { status: 400 });
  }

  const { sectionHtml, currentSchemas, componentType } = body;

  if (!sectionHtml) {
    return Response.json({ ok: false, error: 'Missing sectionHtml' }, { status: 400 });
  }

  try {
    const userMessage = `Section HTML (${componentType || 'unknown'} component):\n\n${sectionHtml.substring(0, 6000)}\n\nCurrent JSON-LD schemas:\n${JSON.stringify(currentSchemas || [], null, 2)}\n\nAnalyze the HTML and update the JSON-LD schemas to accurately reflect all visible content. Use the appropriate tool(s).`;

    const res = await fetch('https://api.anthropic.com/v1/messages', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-api-key': env.ANTHROPIC_API_KEY,
        'anthropic-version': '2023-06-01',
      },
      body: JSON.stringify({
        model: 'claude-haiku-4-5-20251001',
        max_tokens: 4096,
        system: SYSTEM_PROMPT,
        tools: SCHEMA_TOOLS,
        tool_choice: { type: 'any' },
        messages: [{ role: 'user', content: userMessage }],
      }),
    });

    if (!res.ok) {
      const err = await res.text();
      throw new Error(`Claude API error ${res.status}: ${err}`);
    }

    const data: any = await res.json();

    // Extract tool use blocks
    const toolCalls = (data.content || []).filter((b: any) => b.type === 'tool_use');

    if (toolCalls.length === 0) {
      // No tool calls — return current schemas unchanged
      return Response.json({ ok: true, schemas: currentSchemas || [] });
    }

    const schemas = executeToolCalls(toolCalls, currentSchemas || []);

    return Response.json({ ok: true, schemas });
  } catch (err: any) {
    return Response.json({ ok: false, error: err.message || 'Schema update failed' }, { status: 500 });
  }
};
